








import Vue from 'vue'

export default Vue.extend({
	props: {
		disabled: {
			type: Boolean,
			required: false,
		},
		name: {
			type: String,
			required: true,
		},
		type: {
			type: String as () => 'button' | 'submit',
			required: false,
			default: 'button',
		},
	},
})
