import { Module } from 'vuex'
import { RootState } from '@/store'

interface DataState {
	data: Data;
}

const store: Module<DataState, RootState> = {
	state: {
		data: {},
	},
	getters: {
		data: (state: DataState) => state.data,
		rows: (state: DataState) => (name: string) => state.data[name],
	},
	mutations: {
		removeData(state: DataState, payload: string) {
			delete state.data[payload]
		},
		updateData(state: DataState, payload: { name: string; rows: DataRow }) {
			state.data[payload.name] = [ ...payload.rows ]
		},
	},
	actions: {
		setData({ commit, state }, data: Data) {
			Object.keys(state.data).forEach(
				key => { commit('removeData', key) }
			)
			Object.keys(data).forEach(
				key => { commit('updateData', { name: key, rows: data[key] }) }
			)
		},
	},
}

export default store
