



















import Vue from 'vue'

export default Vue.extend({
	props: {
		disabled: {
			type: Boolean,
			required: false,
		},
		label: {
			type: String,
			required: false,
		},
		loading: {
			type: Boolean,
			required: false,
		},
		outline: {
			type: Boolean,
			required: false,
		},
		round: {
			type: Boolean,
			required: false,
		},
		small: {
			type: Boolean,
			required: false,
		},
	},
	computed: {
		circle(): HTMLElement | null {
			return this.$el.querySelector('.button-circle')
		},
	},
	methods: {
		onclick(event: MouseEvent) {
			this.circle?.classList.remove('animate')
			setTimeout(() => {
				this.circle?.classList.add('animate')
			}, 0)
			this.$emit('click', event)
		},
	},
})
