



















import Vue from 'vue'
import { Defaults, jsPlumb } from 'jsplumb'
import { mapGetters } from 'vuex'

const jsplumbConf: (el: Element) => Defaults = (el) =>  ({
	Anchor: ['Continuous', { orientation: 1 }],
	DragOptions: { cursor: 'grab', zIndex: 100, containment: 'true' },
	Connector: ['Flowchart', { stub: [40, 60], gap: 10, cornerRadius: 5, alwaysRespectStubs: true }],
	ConnectionOverlays: [
		['Arrow', { direction: 1, location: 1, width: 15, paintStyle: { fill: '#ccc' } }],
	],
	Container: el,
	Endpoint: 'Blank',
	PaintStyle: { stroke: '#ccccccaa', strokeWidth: 2 },
})

export default Vue.extend({
	props: {
		name: {
			type: String,
			required: true,
		},
	},
	data() {
		return {
			mounted: false,
			rerender: 0,
		}
	},
	computed: {
		...mapGetters({
			readonly: 'querying',
			tables: 'tables',
		}),
	},
	watch: {
		mounted() {
			if (this.mounted === true) this.initialize()
		},
		tables() {
			this.mounted = false
			this.rerender++
		},
	},
	methods: {
		initialize() {
			const instance = jsPlumb.getInstance(jsplumbConf(this.$el))

			this.tablePosition()

			instance.ready(() => {
				document.querySelectorAll('.sq-table').forEach(
					el => instance?.draggable(el)
				)

				Object.values(this.tables as Indexable<Table>).forEach(
					table => {
						const foreigns = table
							.fields
							.filter(field => field.foreign)
							.map(
								field => ({
									table: Object.keys(field.foreign || {})[0],
									tableField: Object.values(field.foreign || {})[0],
									field: field.name,
								})
							)
						
						foreigns.forEach(
							relation => {
								instance.connect({
									source: table.name,
									target: relation.table,
									label: `${relation.field}:${relation.tableField}`,
								})
							}
						)
					}
				)
			})
		},
		tablePosition() {
			const { offsetWidth } = this.$el as HTMLElement
			const perLine = Math.floor((offsetWidth - 40) / 320)
			let maxHeight = 0

			this.$el.querySelectorAll('.canvas-table').forEach(
				(table, i) => {
					const { style, offsetHeight } = table as HTMLElement
					if (offsetHeight > maxHeight) maxHeight = offsetHeight + 350
					style.left = `${(i % perLine) * 320 + 40}px`
					style.top = `${Math.floor(i / perLine) * maxHeight + 100}px`
				}
			)
		},
	},
})
