


























































































import Vue from 'vue'
import { mapGetters } from 'vuex'
import { FieldType } from '@/enums'

export default Vue.extend({
	props: {
		field: {
			type: Object as () => Field,
			required: true,
		},
		foreignOptions: {
			type: Object as () => Indexable<string[]>,
			required: true,
		},
		types: {
			type: Array as () => FieldType[],
			required: true,
		},
	},
	data() {
		const editable: Field = { ...this.field }

		return {
			foreign: {
				table: editable.foreign ? Object.keys(editable.foreign)[0] : '',
				field: editable.foreign ? Object.values(editable.foreign)[0] : '',
			},
			editable,
			editing: !editable.name.length && !editable.type.length,
		}
	},
	computed: {
		...mapGetters({
			readonly: 'querying',
		}),
	},
	methods: {
		oneditcancel() {
			this.editable = { ...this.field }
			this.editing = false
			this.$emit('cancel')
		},
		oneditconfirm() {
			this.editing = false
			this.$emit('change', { ...this.editable })
		},
	},
	watch: {
		foreign: {
			deep: true,
			handler() {
				if (this.foreign.table.length && this.foreign.field.length) {
					this.editable.foreign = {
						[this.foreign.table]: this.foreign.field
					}
				} else {
					this.editable.foreign = {
						table: '',
						field: '',
					}
				}
			},
		},
		editable: {
			deep: true,
			handler() {
				console.log('editable')
				console.log(this.editable.primary, this.editable.unique, this.editable.null, this.editable.notnull)
			},
		},
	},
})
