



















import Vue from 'vue'

export default Vue.extend({
	model: {
		event: 'close',
	},
	props: {
		name: {
			type: String,
			required: true,
		},
		value: {
			type: Boolean,
			required: true,
		},
	},
	data() {
		return {
			show: false,
		}
	},
	watch: {
		value() {
			this.show = this.value
		},
	},
})
