export class Generator {
	static createTable(table: Table): string {
		const columns = '\n' + table.fields.map(
			field => `\t${field.name} ${field.type}${field.size ? `(${field.size})` : ''}`
		).join(',\n')
		return `CREATE TABLE ${table.name} (${columns}\n);`
	}

	static insertTable(table: Table, data: DataRow): string {
		const values = data.map(
			row => `\n\t(${table.fields.map(field => row[field.name]).join(',')})`
		).join(',')
		return `INSERT INTO ${table.name} VALUES ${values};`
	}

	static loadSQL(schema: Schema, data: Data) {
		const ddl = Object.values(schema.tables).map(
			table => Generator.createTable(table)
		).join('\n\n').concat('\n\n')

		const dml = Object.values(schema.tables).map(
			table => Generator.insertTable(table, data[table.name])
		).join('\n\n')

		return ddl.concat(dml)
	}

	static export(schema: Schema, data: Data): Blob {
		const sql = Generator.loadSQL(schema, data)
		return new Blob([sql], { type: 'application/sql' })
	}
}
