






















import Vue from 'vue'

export default Vue.extend({
	props: {
		label: {
			type: String,
			required: false,
		},
		options: {
			type: Array,
			required: true
		},
		placeholder: {
			type: String,
			required: false,
		},
	},
	data() {
		return {
			open: false,
			option: '',
		}
	},
	computed: {
		input(): HTMLInputElement {
			return this.$el.querySelector('input') as HTMLInputElement
		},
		hasOptions(): boolean {
			return this.options.length > 0
		},
	},
	methods: {
		onblur() {
			setTimeout(() => {
				this.open = false
			}, 100)
		},
		onopen() {
			if (!this.hasOptions) return
			this.open = true
			this.$emit('selecting')
		},
		onselect(option: string) {
			this.option = option
			this.$emit('select', option)
			if (this.open) this.open = false
		},
	}
})
