










































import Vue from 'vue'

export default Vue.extend({
	props: {
		readonly: {
			type: Boolean,
			required: false,
		},
		table: {
			type: Object as () => Table,
			required: true
		},
	},
	computed: {
		nameInput(): HTMLInputElement {
			return this.$el.querySelector('.sq-table-name input') as HTMLInputElement
		},
		tableEl(): HTMLTableElement {
			return this.$el.querySelector('table') as HTMLTableElement
		},
		rows(): HTMLCollection {
			return this.tableEl.tBodies[0].rows
		},
	},
	mounted() {
		this.$emit('mounted')
	},
})
