


























import Vue from 'vue'
import { mapGetters } from 'vuex'
import Rows from '@/components/table/Rows.vue'
import Fields from '@/components/table/Fields.vue'

export interface TableSave {
	name: string;
	rows: object[];
	fields: Field[];
}

export default Vue.extend({
	components: {
		Fields,
		Rows,
	},
	model: {
		event: 'close',
	},
	props: {
		name: {
			type: String,
			required: false,
		},
		value: {
			type: Boolean,
			required: true,
		},
	},
	data() {
		return {
			editable: {
				fields: [] as Field[],
				name: '',
				rows: [] as object[],
			} as TableSave,
			ready: false,
			show: this.value,
		}
	},
	computed: {
		storeData(): object[] {
			return this.$store.getters.rows(this.name)
		},
		storeTable(): Table {
			return this.$store.getters.table(this.name)
		},
		...mapGetters({
			readonly: 'querying',
		}),
	},
	methods: {
		onfieldschange(fields: Field[]) {
			this.editable.fields.splice(0, this.editable.fields.length)
			this.editable.fields.push(...fields)
		},
		onrowschange(rows: object[]) {
			this.editable.rows.splice(0, this.editable.rows.length)
			this.editable.rows.push(...rows)
		},
		onsave() {
			this.$emit('save', this.editable)
			this.show = false
			setTimeout(() => {
				this.$emit('close', false)
			}, 0)
		},
	},
	mounted() {
		if (!this.storeData || !this.storeTable) {
			this.ready = true
			return
		}

		this.editable = {
			fields: [ ...this.storeTable.fields ],
			name: this.storeTable.name,
			rows: [ ...this.storeData ],
		}

		this.ready = true
	},
})
