




















import Vue from 'vue'
import { v4 } from 'uuid'

export default Vue.extend({
	props: {
		result: {
			required: true,
			type: Array as () => Array<DataItem>,
		},
	},
	computed: {
		columns(): string[] {
			return this.result.length ? Object.keys(this.result[0]): []
		},
	},
	methods: {
		uuid() {
			return v4()
		},
	},
})
