





































import Vue from 'vue'
import { mapGetters } from 'vuex'
import Result from '@/components/terminal/Result.vue'

const quoteWithDelimiter = /"[^"]*;[^"]*"/g
const selectStatement = /^\s*SELECT/i

export default Vue.extend({
	components: {
		Result,
	},
	data() {
		return {
			error: '',
			input: '',
			query: '',
			results: [] as (DataItem | DataRow)[],
		}
	},
	computed: {
		multiple(): boolean {
			return this.results.length > 0 && this.results[0] instanceof Array
		},
		...mapGetters({
			SQLite: 'SQLite',
		}),
	},
	methods: {
		approve(input: string) {
			const statements = input
				.replaceAll(quoteWithDelimiter, '""')
				.split(';')
				.filter(statement => statement.length)
			return statements.every(statement => selectStatement.test(statement))
		},
		onenter() {
			this.results = []
			this.error = ''
			this.query = this.input
			this.input = ''

			try {
				if (!this.approve(this.query)) {
					throw Error('Forbidden statement. Please write SQL queries using SELECT')
				}
				this.results = this.SQLite(this.query)
			} catch (error) {
				this.error = error.message
			}
		},
	},
})
