import { Module } from 'vuex'
import { RootState } from '@/store'

const maxTables = 18

const store: Module<Schema, RootState> = {
	state: {
		name: '',
		tables: {},
	},
	getters: {
		name: (state: Schema) => state.name,
		schema: (state: Schema) => ({ ...state }),
		table: (state: Schema) => (name: string) => state.tables[name],
		tables: (state: Schema) => state.tables,
	},
	mutations: {
		clearTables(state) {
			state.tables = {}
		},
		nameSchema(state, payload: string) {
			state.name = payload
		},
		removeTable(state, payload: string) {
			delete state.tables[payload]
			state.tables = {
				...state.tables,
			}
		},
		updateTable(state, { name, table }: { name: string; table: Table }) {
			const names = Object.keys(state.tables)
			if (!names.includes(name) && names.length == maxTables) throw new Error()
			state.tables[name] = table
			state.tables = {
				...state.tables,
			}
		},
	},
	actions: {
		newSchema({ commit }, name: string) {
			commit('clearTables')
			commit('nameSchema', name)
		},
	},
}

export default store
