


















import Vue, { VNode } from 'vue'

interface TabProps {
	title: string;
}

Vue.component('vnode', {
	functional: true,
	props: {
		node: {
			type: Object,
			required: true,
		},
	},
	render(_, context) {
		return context.props.node
	},
})

export default Vue.extend({
	data() {
		return {
			current: 0,
		}
	},
	computed: {
		tabs(): VNode[] {
			return this.$slots.default?.filter(n => n.componentOptions?.tag === 'Tab') || []
		},
		titles(): string[] {
			return this.tabs.map(t => (t.componentOptions?.propsData as TabProps).title)
		},
	},
	methods: {
		ontabclick(index: number) {
			this.current = index
		},
	},
})
