import Vue from 'vue'
import App from './App.vue'
import './registerServiceWorker'
import router from './router'
import store from './store'

import EditableInput from '@/ui/EditableInput.vue'
import Icon from '@/ui/Icon.vue'
import IconButton from '@/ui/IconButton.vue'
import Modal from '@/ui/Modal.vue'
import Sidebar from '@/ui/Sidebar.vue'
import SqButton from '@/ui/SqButton.vue'
import SqInput from '@/ui/SqInput.vue'
import SqSelect from '@/ui/SqSelect.vue'
import SqTable from '@/ui/SqTable.vue'
import Tab from '@/ui/Tab.vue'
import Tabs from '@/ui/Tabs.vue'

Vue.component('EditableInput', EditableInput)
Vue.component('Icon', Icon)
Vue.component('IconButton', IconButton)
Vue.component('Modal', Modal)
Vue.component('Sidebar', Sidebar)
Vue.component('SqButton', SqButton)
Vue.component('SqInput', SqInput)
Vue.component('SqSelect', SqSelect)
Vue.component('SqTable', SqTable)
Vue.component('Tab', Tab)
Vue.component('Tabs', Tabs)

Vue.config.productionTip = false

new Vue({
	router,
	store,
	render: (h) => h(App),
}).$mount('#app')
