export default "CREATE TABLE cartoon (\r\n  id tinyint(4) NOT NULL AUTO_INCREMENT,\r\n  name varchar(100) NOT NULL,\r\n  ranking smallint(6) NOT NULL,\r\n  votesUp smallint(6) NOT NULL,\r\n  votesDown smallint(6) NOT NULL,\r\n  PRIMARY KEY (id)\r\n);\r\n\r\nCREATE TABLE producerCompany (\r\n  id tinyint(4) NOT NULL AUTO_INCREMENT,\r\n  name varchar(100) NOT NULL,\r\n  country char(2) NOT NULL,\r\n  PRIMARY KEY (id)\r\n);\r\n\r\nCREATE TABLE cartoon_producerCompany (\r\n  cartoonId tinyint(4) NOT NULL,\r\n  producerCompanyId tinyint(4) NOT NULL,\r\n  PRIMARY KEY (cartoonId,producerCompanyId),\r\n  FOREIGN KEY (cartoonId) REFERENCES cartoon (id),\r\n  FOREIGN KEY (producerCompanyId) REFERENCES producerCompany (id)\r\n);\r\n\r\nCREATE TABLE chrcter (\r\n  id tinyint(4) NOT NULL AUTO_INCREMENT,\r\n  cartoonId tinyint(4) NOT NULL,\r\n  name varchar(100) NOT NULL,\r\n  human varchar(1) DEFAULT '0',\r\n  gender char(1) NOT NULL,\r\n  PRIMARY KEY (id),\r\n  FOREIGN KEY (cartoonId) REFERENCES cartoon (id)\r\n);\r\n\r\nCREATE TABLE genre (\r\n  cartoonId tinyint(4) NOT NULL,\r\n  genreName varchar(30) NOT NULL,\r\n  PRIMARY KEY (cartoonId,genreName),\r\n  FOREIGN KEY (cartoonId) REFERENCES cartoon (id)\r\n);\r\n\r\nCREATE TABLE creator (\r\n  id tinyint(4) NOT NULL AUTO_INCREMENT,\r\n  cartoonId tinyint(4) NOT NULL,\r\n  name varchar(100) NOT NULL,\r\n  gender char(1) NOT NULL,\r\n  PRIMARY KEY (id),\r\n  FOREIGN KEY (cartoonId) REFERENCES cartoon (id)\r\n);\r\n\r\nINSERT INTO cartoon VALUES\r\n\t(1,'Bob Esponja Cala Quadrada',4,16627,9509),\r\n\t(2,'Futurama',7,13302,8565),\r\n\t(3,'Adventure Time',33,8154,4717),\r\n\t(4,'O Incrvel Mundo de Gumball',122,1401,1283),\r\n\t(5,'Bobs Burgers',92,2105,1514),\r\n\t(6,'Avatar: A Lenda de Aang',9,17158,8002),\r\n\t(7,'Padrinhos Mgicos',16,9211,5038),\r\n\t(8,'Trs Espis Demais',111,1599,1654),\r\n\t(9,'Coragem, o Co Covarde',19,8197,4239),\r\n\t(10,'X-Men',30,6355,4047);\r\n\r\nINSERT INTO producerCompany VALUES\r\n\t(1,'Nickelodeon A