import parser from 'mini-sql'
import { CreateTable, Column, Constraint, SqlCommand, Insert } from '@/sql/types'
import store from '@/store'

export class Interpreter {
	private static parse = (input: string) => parser.parse(input)

	static createTable(statement: CreateTable) {
		const table: Table = { name: statement.name, fields: [] }
		const columns = statement.constraints.filter(constr => constr.column)
		const constraints = statement.constraints.filter(constr => constr.constraint)

		columns.forEach(
			constr => {
				if (! constr.column) return
				const {
					name,
					type,
					size,
					precision,
					constraints,
				}: Column = constr.column
				const field: Field = { name, type }
				if (size) field.size = size
				if (precision) field.precision = precision
				if (constraints) {
					if (constraints.default) field.default = constraints.default
					if (constraints.primary) field.primary = constraints.primary
					if (constraints.unique) field.unique = constraints.unique
					if (constraints.null_allowed) field.null = constraints.null_allowed
					if (constraints.never_null) field.notnull = constraints.never_null
				}
				table.fields.push(field)
			}
		)

		constraints.forEach(
			constr => {
				if (!constr.constraint) return
				const {
					primary,
					unique,
					foreign,
				}: Constraint = constr.constraint
				
				primary?.forEach(key => {
					const field = table.fields.find(f => f.name === key)
					if (field) field.primary = true
				})
				unique?.forEach(key => {
					const field = table.fields.find(f => f.name === key)
					if (field) field.unique = true
				})
				foreign?.keys?.forEach((key, index) => {
					const field = table.fields.find(f => f.name === key)
					if (field) field.foreign = { [foreign.table]: foreign.identifiers[index] }
				})
			}
		)

		store.commit('updateTable', { name: table.name, table })
	}

	static insert(statement: Insert) {
		if (statement.reference.name && statement.values) {
			const table = store.getters.table(statement.reference.name)
			const data = statement.values.map(
				val => val.reduce(
					(r, v, index)	=> ({
						[table.fields[index].name]: v,
						...r,
					}),
					{} as Indexable<string>,
				)
			)

			store.commit('updateData', { name: statement.reference.name, rows: data })
		}
	}

	static async import(name: string, file: string) {
		const commands = Interpreter.parse(file) as SqlCommand[]
		store.dispatch('newSchema', name)

		const tableCommands = commands.filter(({ type }) => type === 'CREATE_TABLE') as CreateTable[]
		tableCommands.forEach(command => this.createTable(command))

		const insertCommands = commands.filter(({ type }) => type === 'INSERT') as Insert[]
		insertCommands.forEach(command => this.insert(command))
	}

	static exec(query: string) {
		const statement = this.parse(query) as SqlCommand[]
		statement.forEach(
			stmt => {
				switch(stmt.type) {
				case 'CREATE_TABLE': return this.createTable(stmt)
				case 'INSERT': return this.insert(stmt)
				}
			}
		)
	}
}
