


























import Vue from 'vue'

export default Vue.extend({
	model: {
		event: 'input',
	},
	props: {
		autofocus: {
			type: Boolean,
			required: false,
		},
		disabled: {
			type: Boolean,
			required: false,
		},
		label: {
			type: String,
			required: false,
		},
		placeholder: {
			type: String,
			required: false,
		},
		readonly: {
			type: Boolean,
			required: false,
		},
		required: {
			type: Boolean,
			required: false,
		},
		type: {
			type: String,
			required: false,
			default: 'text',
		},
		value: {
			type: String || Number,
			required: false,
		},
	},
	data() {
		return {
			focused: false,
			inputModel: this.value || '',
		}
	},
	methods: {
		blur() {
			this.focused = false
			this.$emit('blur')
		},
		focus() {
			this.focused = true
			this.$emit('focus')
		},
	},
	watch: {
		value() {
			this.inputModel = this.value
		},
	},
	mounted() {
		if (this.autofocus) {
			this.$nextTick(() => {
				this.$el.querySelector('input')?.focus()
			})
		}
	},
})
