<template>
	<div class="home">
		<Schema class="home-schema" :key="querying"
		v-if="!mobile" />
		<Mobile v-else />
		<Terminal class="home-terminal"
		@close="$store.dispatch('stopQuery')"
		v-if="querying" />
		<SqButton v-else-if="!mobile" round
		:disabled="!name.length"
		@click="startQuery"
		class="home-query">
			<Icon name="table-select" />
		</SqButton>
	</div>
</template>

<script>
import Vue from 'vue'
import { mapGetters } from 'vuex'
import Mobile from '@/components/Mobile.vue'
import Schema from '@/components/Schema.vue'
import Terminal from '@/components/Terminal.vue'
import { Generator } from '@/sql'

export default Vue.extend({
	components: {
		Mobile,
		Schema,
		Terminal,
	},
	computed: {
		...mapGetters({
			name: 'name',
			mobile: 'appMobile',
			querying: 'querying',
		}),
	},
	methods: {
		startQuery() {
			const { data, name, schema } = this.$store.getters
			const DDL_DML = Generator.loadSQL(schema, data)
			this.$store.dispatch('startQuery', { name, DDL_DML })
		},
	},
})
</script>

<style lang="scss">
.home {
	display: flex;
	height: calc(100vh - 80px);
	position: relative;

	&-schema {
		flex: 1;
	}

	&-terminal {
		width: 35%;
	}

	&-query {
		position: absolute;
		margin: 15px;
		bottom: 0;
		right: 0;
		z-index: 9999;
	}
}
</style>
