






















import Vue from 'vue'

export default Vue.extend({
	props: {
		name: {
			type: String,
			required: true,
		},
		value: {
			type: Boolean,
			required: true,
		},
	},
	data() {
		return {
			editable: this.name,
			show: this.value,
		}
	},
	watch: {
		value() {
			this.show = this.value
		},
	},
})
