









































import Vue from 'vue'
import { mapGetters } from 'vuex'

export default Vue.extend({
	model: {
		event: 'change',
	},
	props: {
		fields: {
			type: Array as () => Field[],
			required: true,
		},
		rows: {
			type: Array as () => Array<object>,
			required: true,
		},
	},
	data() {
		return {
			editable: this.rows ? this.rows.map(el => ({ ...el })) : [],
			editing: null as number | null,
			hovering: null as number | null,
		}
	},
	computed: {
		checkall(): HTMLInputElement | null {
			return this.$el?.querySelector('.rows-checkall')
		},
		...mapGetters({
			readonly: 'querying',
		}),
	},
	methods: {
		emitChange() {
			this.$emit('change', this.editable.map(el => ({ ...el })))
		},
		onadd() {
			this.editable.push({ })
			this.editing = this.editable.length - 1
		},
		onedit(index: number) {
			if (this.editing !== null) {
				this.editable[this.editing] = this.rows[this.editing]
			}
			this.editing = index
		},
		oneditcancel() {
			if (this.editing === null) return
			if (this.editable.length === this.rows.length + 1) {
				this.editable.splice(this.editable.length - 1, 1)
			} else {
				this.editable[this.editing] = this.rows[this.editing]
			}
			this.editing = null
		},
		oneditsave() {
			this.editing = null
			this.emitChange()
		},
		onremove(index: number) {
			this.editable.splice(index, 1)
			this.emitChange()
		},
	},
})
