export default "CREATE TABLE `Department` (\n  `dname` char(15) NOT NULL UNIQUE,\n  `dnumber` int(11) NOT NULL,\n  `mgrssn` char(9) NOT NULL,\n  `mgrstartdate` date DEFAULT NULL,\n  PRIMARY KEY (`dnumber`)\n);\n\nINSERT INTO `Department` VALUES\n  ('Headquarters',1,'888665555','1981-06-19'),\n  ('Administration',4,'987654321','1995-01-01'),\n  ('Research',5,'333445555','1988-05-22');\n\nCREATE TABLE `Dependent` (\n  `essn` char(9) NOT NULL,\n  `name` char(10) NOT NULL,\n  `sex` char(1) DEFAULT NULL,\n  `bdate` date DEFAULT NULL,\n  `relationship` char(10) DEFAULT NULL,\n  PRIMARY KEY (`essn`,`name`),\n  FOREIGN KEY (`essn`) REFERENCES `Employee` (`ssn`)\n);\n\nINSERT INTO `Dependent` VALUES\n  ('123456789','Alice','F','1988-12-30','Daughter'),\n  ('123456789','Elizabeth','F','1967-05-05','Spouse'),\n  ('123456789','Michael','M','1988-01-04','Son'),\n  ('333445555','Alice','F','1986-04-05','Daughter'),\n  ('333445555','Joy','F','1958-05-03','Spouse'),\n  ('333445555','Theodore','F','1983-10-25','Son'),\n  ('987654321','Abner','M','1942-02-28','Spouse');\n\n\nCREATE TABLE `Dept_Locations` (\n  `dnumber` int(11) NOT NULL,\n  `dlocation` char(10) NOT NULL,\n  PRIMARY KEY (`dnumber`,`dlocation`),\n  FOREIGN KEY (`dnumber`) REFERENCES `Department` (`dnumber`)\n);\n\nINSERT INTO `Dept_Locations` VALUES\n  (1,'Houston'),\n  (4,'Stafford'),\n  (5,'Bellaire'),\n  (5,'Houston'),\n  (5,'Sugarland');\n\nCREATE TABLE `Employee` (\n  `fname` char(9) NOT NULL,\n  `minit` char(1) DEFAULT NULL,\n  `lname` char(8) NOT NULL,\n  `ssn` char(9) NOT NULL,\n  `bdate` date DEFAULT NULL,\n  `address` char(25) DEFAULT NULL,\n  `sex` char(1) DEFAULT NULL,\n  `salary` decimal(7,2) DEFAULT NULL,\n  `superssn` char(9) DEFAULT NULL,\n  `dno` int(11) NOT NULL DEFAULT '1',\n  PRIMARY KEY (`ssn`),\n  FOREIGN KEY (`dno`) REFERENCES `Department` (`dnumber`)\n);\n\nINSERT INTO `Employee` VALUES\n  ('John','B','Smith','123456789','1965-01-09','731 Fondren, Houston, TX','M',30000.00,'333445555',5),\n  ('Franklin','T','Wong','333445555','1955-12-08','638 Voss, Houston, TX','M',40000.00,'888665555',5),\n  ('Joyce','A','English','453453453','1972-07-31','5631 Rice, Houston, TX','F',25000.00,'333445555',5),\n  ('Ramesh','K','Narayan','666884444','1962-09-15','975 Fire Oak, Humble, TX','M',38000.00,'333445555',5),\n  ('James','E','Borg','888665555','1937-11-10','450 Stone, Houston, TX','M',55000.00,NULL,1),\n  ('Jennifer','S','Wallace','987654321','1941-06-20','291 Berry, Bellaire, TX','F',43000.00,'888665555',4),\n  ('Ahmad','V','Jabbar','987987987','1969-03-29','980 Dallas, Houston, TX','M',25000.00,'987654321',4),\n  ('Alicia','J','Zelaya','999887777','1968-01-19','3321 Castle, Spring, TX','F',25000.00,'987654321',4);\n\nCREATE TABLE `Project` (\n  `pname` char(15) NOT NULL,\n  `pnumber` int(11) NOT NULL,\n  `plocation` char(10) DEFAULT NULL,\n  `dnum` int(11) NOT NULL,\n  PRIMARY KEY (`pnumber`),\n  UNIQUE (`pname`),\n  FOREIGN KEY (`dnum`) REFERENCES `Department` (`dnumber`)\n);\n\nINSERT INTO `Project` VALUES\n  ('ProductX',1,'Bellaire',5),\n  ('ProductY',2,'Sugarland',5),\n  ('ProductZ',3,'Houston',5),\n  ('Computerization',10,'Stafford',4),\n  ('Reorganization',20,'Houston',1),\n  ('Newbenefits',30,'Stafford',4);\n\n\nCREATE TABLE `Works_On` (\n  `essn` char(9) NOT NULL,\n  `pno` int(11) NOT NULL,\n  `hours` decimal(5,1) NOT NULL,\n  PRIMARY KEY (`essn`,`pno`),\n  FOREIGN KEY (`essn`) REFERENCES `Employee` (`ssn`),\n  FOREIGN KEY (`pno`) REFERENCES `Project` (`pnumber`)\n);\n\nINSERT INTO `Works_On` VALUES\n  ('123456789',1,32.5),\n  ('123456789',2,7.5),\n  ('333445555',2,10.0),\n  ('333445555',3,10.0),\n  ('333445555',10,10.0),\n  ('333445555',20,10.0),\n  ('453453453',1,20.0),\n  ('453453453',2,20.0),\n  ('666884444',3,40.0),\n  ('888665555',20,0.5),\n  ('987654321',20,15.0),\n  ('987654321',30,20.0),\n  ('987987987',10,35.0),\n  ('987987987',30,5.0),\n  ('999887777',10,10.0),\n  ('999887777',30,30.0);\n";