


















import Vue from 'vue'

export default Vue.extend({
	model: {
		event: 'save',
	},
	props: {
		disabled: {
			type: Boolean,
			required: false
		},
		label: {
			type: String,
			required: false,
		},
		placeholder: {
			type: String,
			required: false
		},
		type: {
			type: String,
			required: false,
			default: 'text'
		},
		value: {
			type: String,
			required: false
		},
	},
	computed: {
		input(): HTMLInputElement {
			return this.$el.querySelector('input') as HTMLInputElement
		},
	},
	data() {
		return {
			inputModel: this.value,
			editing: false,
		}
	},
	methods: {
		edit() {
			if (this.disabled) return
			this.editing = true

			setTimeout(() => {
				this.input.focus()
			}, 30)
		},
		cancel() {
			this.editing = false
			this.inputModel = this.value
		},
		save() {
			this.editing = false
			this.$emit('save', this.inputModel)
		},
	},
	watch: {
		value() {
			this.inputModel = this.value
		},
	},
})
