




























import Vue from 'vue'
import { mapGetters } from 'vuex'
import TField from './Field.vue'
import { TypeEnum } from '@/enums'

export default Vue.extend({
	components: {
		TField,
	},
	props: {
		table: {
			required: false,
			type: Object as () => Table,
		},
	},
	data() {
		return {
			editable: this.table.fields.map(field => ({ ...field } as Field)),
			newField: null as Field | null,
			types: Object.keys(TypeEnum),
		}
	},
	computed: {
		...mapGetters({
			readonly: 'querying',
			tables: 'tables',
		}),
		foreignOptions(): Indexable<string[]> {
			return Object.values(this.tables as Indexable<Table>)
				.reduce(
					(options, table) => {
						if (table.name !== this.table.name) {
							options[table.name] = table.fields.map(field => field.name)
						}
						return options
					}, {} as Indexable<string[]>
				)
		},
	},
	methods: {
		emitChange() {
			this.$emit('change', this.editable.map(field => ({ ...field })))
		},
		onadd() {
			this.newField = {
				name: '',
				type: '',
				size: undefined,
			}
		},
		onaddconfirm(field: Field) {
			this.editable.push({ ...field })
			this.newField = null
			this.emitChange()
		},
		onchange($event: Field, index: number) {
			this.editable[index] = { ...$event }
			this.emitChange()
		},
		onremove(index: number) {
			this.editable.splice(index, 1)
			this.emitChange()
		},
	},
})
