









































import Vue from 'vue'
import { mapGetters } from 'vuex'
import Canvas from '@/components/schema/Canvas.vue'
import CreateSchema from '@/components/schema/CreateSchema.vue'
import Download from '@/components/schema/Download.vue'
import RemoveTable from '@/components/schema/RemoveTable.vue'
import RenameSchema from '@/components/schema/RenameSchema.vue'
import SchemaName from '@/components/schema/Name.vue'
import TableSidebar, { TableSave } from '@/components/table/TableSidebar.vue'
import Upload from '@/components/schema/Upload.vue'

export default Vue.extend({
	components: {
		Canvas,
		CreateSchema,
		Download,
		RemoveTable,
		RenameSchema,
		SchemaName,
		TableSidebar,
		Upload,
	},
	data() {
		return {
			addingSchema: false,
			addingTable: false,
			editingTable: false,
			focusedTable: '',
			renamingSchema: false,
			removingTable: false,
		}
	},
	computed: {
		...mapGetters({
			name: 'name',
		})
	},
	methods: {
		onaddschemaconfirm(name: string) {
			this.$store.dispatch('newSchema', name)
			this.addingSchema = false
		},
		onaddtableconfirm({ name, fields, rows }: TableSave) {
			this.$store.commit('updateTable', { name, table: { name, fields } })
			this.$store.commit('updateData', { name, rows })
		},
		onedittable(table: string) {
			this.focusedTable = table
			this.editingTable = true
		},
		onedittableconfirm({ name, fields, rows }: TableSave) {
			this.$store.commit('updateTable', { name, table: { name, fields } })
			this.$store.commit('updateData', { name: name, rows })
		},
		onremovetable(table: string) {
			this.focusedTable = table
			this.removingTable = true
		},
		onremovetableconfirm() {
			this.$store.commit('removeTable', this.focusedTable)
			this.removingTable = false
		},
		onrenameschemaconfirm(name: string) {
			this.$store.commit('nameSchema', name)
			this.renamingSchema = false
		},
	},
})
