import { Module } from 'vuex'
import { RootState } from '@/store'
import alasql from 'alasql'

interface SQLState {
	// eslint-disable-next-line
	db: undefined | string;
	// eslint-disable-next-line
	SQLite: any;
}

interface QuerySchema {
	name: string;
	DDL_DML: string;
}

const store: Module<SQLState, RootState> = {
	state: {
		db: undefined,
		SQLite: alasql,
	},
	getters: {
		querying: (state: SQLState) => state.db !== undefined,
		SQLite: (state: SQLState) => state.SQLite,
	},
	mutations: {
		createSchema(state, payload: string) {
			const { databaseid } = new state.SQLite.Database(payload)
			state.SQLite(`use schema ${databaseid};`)
			state.db = databaseid
		},
		dropSchema(state) {
			state.SQLite(`drop schema ${state.db};`)
			state.db = undefined
		},
		executeCommands(state, payload: string) {
			return state.SQLite(payload)
		},
	},
	actions: {
		startQuery({ commit }, schema: QuerySchema) {
			commit('createSchema', schema.name)
			commit('executeCommands', schema.DDL_DML)
		},
		stopQuery({ commit }) {
			commit('dropSchema')
		},
	},
}

export default store
