






































import Vue from 'vue'
import { mapGetters } from 'vuex'

export default Vue.extend({
	data() {
		return {
			open: false,
		}
	},
	computed: {
		...mapGetters({
			name: 'name',
			readonly: 'querying',
			schemas: 'schemas',
		})
	},
	methods: {
		close() {
			setTimeout(() => {
				this.open = false
			}, 0)
		},
		async onadd() {
			await this.$emit('add')
			this.close()
		},
		async onrename() {
			await this.$emit('rename')
			this.close()
		},
		async onselect(name: string) {
			await this.$store.dispatch('localSchema', name)
			this.close()
		},
	},
})
