import Vue from 'vue'
import Vuex from 'vuex'

import dataStore from '@/store/data'
import schemaStore from '@/store/schema'
import queryStore from '@/store/query'
import { Interpreter } from '@/sql'

Vue.use(Vuex)

export interface RootState {
	appLoading: boolean;
	appMobile: boolean;
}

export default new Vuex.Store<RootState>({
	modules: {
		dataStore,
		queryStore,
		schemaStore,
	},
	state: {
		appLoading: false,
		appMobile: window.innerWidth < 500,
	},
	mutations: {
		startLoading(state: RootState) {
			state.appLoading = true
		},
		stopLoading(state: RootState) {
			state.appLoading = false
		},
	},
	actions: {
		async loadSchema({}, file: File) {
			const name = file.name.replace('.sql', '')
			const content = await file.text()

			Interpreter.import(name, content)
		},
		async localSchema({}, name: string) {
			const db = (await require(`@/store/databases/${name}.sql`)).default
			Interpreter.import(name, db)
		},
		exec({}, query: string) {
			Interpreter.exec(query)
		},
	},
	getters: {
		appLoading: state => state.appLoading,
		appMobile: state => state.appMobile,
		schemas: () => ['cartoon', 'company' ],
	},
})
